.header {
  width: $mobile-width;
  position: relative;
  background-color: $main-color-second;

  @media (min-width: $tablet-width) {
    display: flex;
    width: $tablet-width;
  }

  @media (min-width: $desktop-width) {
    width: $desktop-width;
  }
}

.header__logo {
  width: 185px;
  height: 56px;
  display: flex;
  margin: 0 auto;
  position: relative;
  z-index: 5;

  @media (min-width: $tablet-width) {
    position: absolute;
    top: 0;
    right: 50%;
    margin-right: -92.5px;
  }
}

.header__logo-image {
  &:hover {
    opacity: 0.9;
  }

  &:active {
    opacity: 0.7;
  }
}
