@font-face {
  font-family: "PT Sans";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/ptsans.woff2") format("woff2"), url("../fonts/ptsans.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "PT Sans";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/ptsansbold.woff2") format("woff2"), url("../fonts/ptsansbold.woff") format("woff");
  font-display: swap;
}
body {
  height: 100%;
  margin: 0;
  font-family: "PT Sans", "Arial", sans-serif;
  font-style: normal;
  font-size: 14px;
  line-height: 21px;
  text-align: center;
  color: #333333;
  background-color: #ffffff;
  text-transform: uppercase;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

.page {
  background-color: #f2f2f2;
  width: 100%;
}

.page__body {
  width: 320px;
  margin: 0 auto;
  box-shadow: 0 5px 15px rgba(0, 1, 1, 0.2);
}
@media (min-width: 768px) {
  .page__body {
    width: 768px;
  }
}
@media (min-width: 1200px) {
  .page__body {
    width: 1200px;
  }
}

.header {
  width: 320px;
  position: relative;
  background-color: #ffffff;
}
@media (min-width: 768px) {
  .header {
    display: flex;
    width: 768px;
  }
}
@media (min-width: 1200px) {
  .header {
    width: 1200px;
  }
}

.header__logo {
  width: 185px;
  height: 56px;
  display: flex;
  margin: 0 auto;
  position: relative;
  z-index: 5;
}
@media (min-width: 768px) {
  .header__logo {
    position: absolute;
    top: 0;
    right: 50%;
    margin-right: -92.5px;
  }
}

.header__logo-image:hover {
  opacity: 0.9;
}
.header__logo-image:active {
  opacity: 0.7;
}

.container {
  margin: 0 auto;
}

.nav__toggle {
  width: 23px;
  height: 22px;
  border: none;
  padding: 0;
  position: absolute;
  cursor: pointer;
  top: 17px;
  right: 20px;
  z-index: 15;
}
.nav__toggle:hover {
  opacity: 0.6;
}
.nav__toggle:active {
  opacity: 0.3;
}
@media (min-width: 768px) {
  .nav__toggle {
    display: none;
  }
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  width: 320px;
  margin-top: 40px;
  z-index: 10;
}

.nav__link {
  font-size: 18px;
  line-height: 26px;
  font-weight: 700;
  color: #ffffff;
  text-decoration: none;
  display: block;
  padding: 17px 10px 12px 10px;
  background-color: #81b3d2;
  box-shadow: 0 1px 0 rgba(1, 1, 1, 0.2), inset 0 1px 0 rgba(255, 255, 255, 0.2);
}
@media (min-width: 768px) {
  .nav__link {
    font-size: 14px;
    line-height: 21px;
    color: #000000;
    background-color: #ffffff;
    padding: 18px;
    width: 153px;
    box-shadow: none;
  }
}
@media (min-width: 1200px) {
  .nav__link {
    width: 240px;
  }
}

@media (min-width: 768px) {
  .nav__item:nth-of-type(4n+2) {
    margin-right: 153px;
  }
}
@media (min-width: 1200px) {
  .nav__item:nth-of-type(4n+2) {
    margin-right: 240px;
  }
}

.nav--closed .nav__toggle {
  background-image: url("../../img/menu-icon-open.svg");
  background-color: #ffffff;
}

.nav--opened .nav__toggle {
  background-image: url("../../img/menu-icon-close.svg");
  background-color: transparent;
}

@media (min-width: 320px) {
  .nav--closed .nav__list {
    display: none;
  }

  .nav--opened .nav__list {
    position: absolute;
    top: 0;
    margin-top: 0;
  }

  .nav--nojs .nav__toggle {
    display: none;
  }

  .nav--nojs .nav__list {
    display: block;
    margin-top: 40px;
    position: static;
  }

  .nav__link[href]:hover {
    background-color: #669ec0;
  }

  .nav__link[href]:active {
    background-color: #5496bd;
    color: rgba(0, 0, 0, 0.3);
  }
}
@media (min-width: 768px) {
  .nav .nav__list {
    display: flex;
    width: 768px;
    margin-top: 0;
    flex-wrap: wrap;
  }

  .nav__link[href]:hover {
    color: #81b3d2;
    background-color: #ffffff;
  }

  .nav__link[href]:active {
    color: rgba(0, 0, 0, 0.3);
    background-color: #ffffff;
  }
}
@media (min-width: 1200px) {
  .nav .nav__list {
    width: 1200px;
  }
}
.nav__item--current .nav__link {
  color: #766357;
}

