    font-size: 21px;
    line-height: 26px;
    font-weight: 700;
  }
}

.modal__button--error {
  width: 180px;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .modal__button--error {
    width: 272px;
  }
}

.modal__button--success {
  width: 280px;
}
@media (min-width: 768px) {
  .modal__button--success {
    width: 458px;
  }
}

.modal__container--error-close {
  display: none;
}

.modal__container--success-close {
  display: none;
}